.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.45);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: linear-gradient(180deg, #ffffff 0%, #f9f9ff 100%);
  border-radius: 20px;
  padding: 30px;
  width: 700px;
  box-shadow: 0 10px 40px rgba(0,0,0,0.15);
  position: relative;
}

.modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 22px;
  cursor: pointer;
  color: #aaa;
}

.modal-close:hover {
  color: #333;
}

.modal-header h2 {
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  text-align: center;
  margin-bottom: 25px;
  font-weight: 700;
  color: #333;
  cursor: pointer;
}

.title-input {
  width: 100%;
  font-size: 1.4rem;
  text-align: center;
  border: none;
  border-bottom: 2px solid #667eea;
  outline: none;
  margin-bottom: 25px;
}

.new-project-form {
  display: flex;
  gap: 25px;
}

.image-upload {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fafafa;
  border: 2px dashed #ccc;
  border-radius: 15px;
  min-height: 250px;
  transition: all 0.3s ease;
}

.image-upload:hover {
  border-color: #667eea;
  background: #f4f4ff;
}

.upload-label {
  font-size: 1rem;
  color: #555;
  text-align: center;
  cursor: pointer;
}

.image-preview-container {
  position: relative;
}

.uploaded-image {
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}

.image-remove {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: rgba(0,0,0,0.6);
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  width: 30px;
  height: 30px;
}

.form-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.form-group label {
  font-weight: 600;
  color: #444;
  margin-bottom: 4px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1.8px solid #ddd;
  border-radius: 10px;
  outline: none;
  transition: all 0.3s ease;
}

.form-group input:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102,126,234,0.1);
}

.checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.95rem;
  color: #555;
}

.submit-btn {
  margin-top: 15px;
  padding: 12px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  font-weight: 700;
  font-size: 1rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: 0 6px 15px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.submit-btn:hover {
  background: linear-gradient(135deg, #5a6ee0 0%, #6b41a8 100%);
}
